.titleContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .titleBox {
        margin-bottom: 20px;
        display: flex;
        align-items: center;
    
        img {
            width: 30px;
            height: auto;
            margin-right: 20px;
        }
    
        h1 {
            color: var(--primaryTitle);
            font-weight: 900;
        }
    }

    .turnBackBox {

    }
}

.pokemonProfile {
    display: flex;
    width: 100%;

    .c1 {
        width: 30%;
        display: flex;
        flex-direction: column;

        .littleNavBox {
            background-color: var(--tertiary);
            width: 100%;
            height: 35px;
            margin-bottom: 5px;
            border-radius: 10px;
            display: grid;
            grid-template-columns: 60px auto 60px;
            box-shadow: 1px 1px 2px #00000099;

            .prevBox {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                border-radius: 10px;
                background-color: var(--secondary);
                cursor: pointer;
            }

            .centerBox {
                display: flex;
                justify-content: center;
                align-items: center;
                box-shadow: inset 1 1 2px #000000;
            }

            .nextBox {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                border-radius: 10px;
                background-color: var(--secondary);
                cursor: pointer;
            }

            p {
                color: var(--tertiaryTitle);
                margin: 0;
                font-size: 0.8rem;
                cursor: pointer;
            }
        }

        .pokemonBox {
            position: relative;
            width: 100%;
            align-items: center;

            .pokemonImage {
                position: absolute;
                width: 80%;
                height: auto;
                z-index: 2;
                transform: translate(10%,10%);
            }

            .pokemonCard {
                position: relative;
                width: 100%;
                height: auto;
                z-index: 1;
            }
        }
    }

    .c2 {
        margin-left: 5%;
        width: 64%;
        padding-right: 1%;
        height: calc(100vh - 190px);
        overflow-y: scroll;

        p {
            text-align: justify;
            color: var(--secondaryTitle);
        }

        h2 {
            color: var(--primaryTitle);
            font-size: 2rem;
            font-weight: 900;
            margin: 0px;
        }

        .grid3Columns {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 10px;
        }

        .grid2Columns {
            box-sizing: border-box;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 10px;
        }

        .measurementBox {
            box-sizing: border-box;
            background-color: white;
            width: 100%;
            height: 60px;
            border-radius: 10px;
            padding: 14px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            box-shadow: 1px 1px 2px #00000099;

            img {
                height: 100%;
                width: auto;
            }

            h3 {
                color: var(--secondaryTitle);
                font-size: 1.4rem;
                font-weight: 700;
            }
        }
    }
}